.main{
  position: absolute;
  padding: 50px;
  height: 100vh;
  width: 100%;
}

.screen{
  position: relative;
  border: 1px solid black;
  background: transparent;
  height: 100%;
  padding: 20px
}

.name{
  text-align: left;
  margin: 10px 0px;
}

.navlinks{
  position: absolute;
  display:grid;
  text-align: right;
  bottom: 0;
  right: 0;
  padding: 20px;
}

.h2, .h3, .h4s{
  text-decoration: underline;
}

.h4 {
  margin: 10px 0px;
}

.link{
  font-size: 18px;
  margin: 8px 0;
}

.p {
  text-transform: none;
  white-space: wrap;
}

.box{
  position: relative;
  margin-top: 80px;
}

.text{
  max-width: 500px;
  height: 500px;
  overflow: hidden;
  overflow-y: auto;
}

.scrollContents{
  position: relative;
  width: 100%;    
  height: auto;
  justify-content:center;
  align-items: center;
}
@media (max-width: 750px) {
  .text{
      height: 400px;
  }
}

@media (max-width: 656px) {
  .main{
      max-width: 100%;
  }

  .name, .h4{
      white-space: wrap;
  }

  .box{
      margin-top: 20px;
  }
}
@media (max-width: 506px) {
  .link{
      margin: 6px 0;
  }
}

@media (max-width: 445px) {
  .name{
      font-size: 24px;
  }

  .h3{
      font-size: 16px;
  }

  .box{
      margin-top: 50px;
  }
}
@media (max-width: 270px) {
  .main{
      padding: 10px;
  }
  
  .screen{
      border: 0px;
      padding: 10px
  }
  
}